import crypto from 'crypto';
import { userModal } from '../../../db';

export default async function handler(req, res) {
    if (req.method !== 'POST') {
        return res.status(405).json({ error: 'Method not allowed' });
    }

    const secret = process.env.RAZORPAY_KEY_SECRET;
    const signature = req.headers['x-razorpay-signature'];
    const body = JSON.stringify(req.body);

    const expectedSignature = crypto
        .createHmac('sha256', secret)
        .update(body)
        .digest('hex');

    if (signature !== expectedSignature) {
        return res.status(400).json({ error: 'Invalid signature' });
    }

    const event = req.body;
    if (event.event === 'payment.captured') {
        const paymentEntity = event.payload.payment.entity;
        const razorpayPaymentId = paymentEntity.id;
        const orderId = paymentEntity.order_id;
        // Find user by stored orderId
        const user = await userModal.findOne({ razorpayOrderId: orderId });
        if (user) {
            user.subscriptionStatus = 'active';
            user.subscriptionEndDate = new Date(Date.now() + 30 * 24 * 60 * 60 * 1000); // 30 days
            user.razorpayPaymentId = razorpayPaymentId;
            await user.save();
        }
    }

    res.status(200).json({ received: true });
}
